.dialog {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  max-width: 400px;
  margin: 50px auto;
  transition: transform 0.3s ease;
}

.dialog-enter {
  transform: scale(0.9);
  opacity: 0;
}

.dialog-enter-active {
  transform: scale(1);
  opacity: 1;
  transition: transform 300ms, opacity 300ms;
}

.dialog-exit {
  transform: scale(1);
  opacity: 1;
}

.dialog-exit-active {
  transform: scale(0.9);
  opacity: 0;
  transition: transform 300ms, opacity 300ms;
}

button {
  margin-top: 20px;
}
